.gallery__ {
  &title {
    font-family: Inter;
    font-size: 48px;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: -1.5px;
    text-align: center;
    color: #292929;
    margin-top: 120px;
  }
  &category {
    &-container {
      margin-top: 64px;
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: 100%;
      display: flex;
      justify-content: center;
    }
    &-img {
      display: block;
     
    }
  }
}

@media (max-width: 1100px) {
  .gallery__ {
    &container{
      margin-left: -10%;
    }
    &title {
      display: flex;
      justify-content: center;
     
    }
    &category-container {
      max-width: 90%;
    }
  }
}

@media (max-width: 630px) {
  .gallery__ {
    &title {
      display: flex;
      justify-content: center;
      font-size: 30px;
    }
    &category-container {
      max-width: 90%;
      margin-left: 10%;
      margin-right: 10%;
      
    }
  }
}
